.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

#imgContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.imgs {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 200px;
    margin: 10px;
}

.img {
    height: 100%;
}

.img img {
    width: 100%;
    height: 100%;
}

.name {
    text-align: center;
    font-size: 18px;
    letter-spacing: 1.5px;
}